.sg-btn {
    position: relative;

    .remBorderRadiusAll(0.4);
    .remMinHeight(@sgMiscHeightElements);
    .transition(0s);

     &.sg-btn-small {
        .sg-btn-small-ziggo();
    }

    &.sg-black {
        .sg-btn-ziggo-black()
    }

    &.sg-disabled {
        .sg-btn-ziggo-disabled()
    }

    &.sg-orange {
        .sg-btn-ziggo-orange()
    }

    &.sg-white {
        .sg-btn-ziggo-white()
    }

    &.sg-without-icon {
        .remPaddingRight(@sgPaddingButtonLeft);
    }
}

.sg-btn-small-ziggo() {
    line-height: 0;

    .remFontSize(1.4);
    .remMaxHeight(2.7);
    .remMinHeight(2.7);
    .remMinWidth(9);
    .remPadding(@sgPaddingButtonSmallTop, @sgPaddingButtonSmallRight, @sgPaddingButtonSmallBottom, @sgPaddingButtonSmallLeft);

    &.sg-without-icon {
        .remPaddingRight(@sgPaddingButtonSmallLeft);
    }
    
    &+ .sg-btn-icon {
        &:before {
            .remFontSize(1.1);
            .remLeft(-1.6);
            .remTop(0.3);
        }
    }
}

button.sg-btn.sg-btn-small {
    .remPaddingTop(0.2);
}

.sg-btn-ziggo-black() {
    .sg-give-gradient-to-element(@sgColorButtonBlackStart, @sgColorButtonBlackEnd, @sgColorButtonBlackHoverStart, @sgColorButtonBlackHoverEnd, @sgColorButtonBlackText, @sgColorButtonBlackText, @sgColorButtonBlackBorder, @sgColorButtonBlackBorder);
    .sg-give-active-color-to-element(@sgColorButtonBlackActive, rgba(255,255,255,0.5), @sgColorButtonBlackActiveBorder);
    .box-shadow-multiple(0 2px 2px rgba(0,0,0,.2), inset 0 2px #4b4d4f;);

    &:active {
        .box-shadow(inset 0 3px 5px #000;);
    }
}

.sg-btn-ziggo-orange() {
    .sg-give-gradient-to-element(@sgColorButtonOrangeStart, @sgColorButtonOrangeEnd, @sgColorButtonOrangeHoverStart, @sgColorButtonOrangeHoverEnd, @sgColorButtonOrangeText, @sgColorButtonOrangeText, @sgColorButtonOrangeBorder, @sgColorButtonOrangeBorder);
    .sg-give-active-color-to-element(@sgColorButtonOrangeActive, rgba(255,221,132,0.5), @sgColorButtonOrangeActiveBorder);
    .box-shadow-multiple(0 2px 2px rgba(0,0,0,.2), inset 0 2px rgba(255,221,132,.7););

    &:active {
        .box-shadow(inset 0 3px 5px rgba(0,0,0,.4););
    }
}

.sg-btn-ziggo-white() {
    .sg-give-gradient-to-element(@sgColorButtonWhiteStart, @sgColorButtonWhiteEnd, @sgColorButtonWhiteHoverStart, @sgColorButtonWhiteHoverEnd, @sgColorButtonWhiteText, @sgColorButtonWhiteText, @sgColorButtonWhiteBorder, @sgColorButtonWhiteBorder);
    .sg-give-active-color-to-element(@sgColorButtonWhiteActive, rgba(38,39,39,0.5), @sgColorButtonWhiteActiveBorder);
    .box-shadow-multiple(0 2px 2px rgba(0,0,0,.2), inset 0 2px #fff;);

    &:active {
        .box-shadow(inset 0 3px 5px rgba(0,0,0,.4));
    }
    
    &+ .sg-btn-icon:before {
        color: @sgColorGrey1;
    }
}

.sg-btn-ziggo-disabled() {
    .sg-give-gradient-to-element(@sgColorButtonDisabledStart, @sgColorButtonDisabledEnd, @sgColorButtonDisabledStart, @sgColorButtonDisabledEnd, @sgColorButtonDisabledText, @sgColorButtonDisabledText, @sgColorButtonDisabledBorder, @sgColorButtonDisabledBorder);
    .box-shadow(inset 0 2px #fff);
}
